import Image from "next/image"
import { Star } from "lucide-react"

export default function AppointmentsTestimonials() {
  const testimonials = [
    {
      name: "Jean Mutesi",
      location: "Kigali",
      image: "/placeholder.svg?height=100&width=100&text=JM",
      rating: 5,
      text: "The appointment booking process was incredibly simple. I was able to find a specialist, book a time that worked for me, and receive confirmation all within minutes. The reminders were helpful too!",
    },
    {
      name: "Emmanuel Hakizimana",
      location: "Butare",
      image: "/placeholder.svg?height=100&width=100&text=EH",
      rating: 5,
      text: "As someone living in a rural area, being able to book appointments online has been life-changing. I no longer have to travel just to schedule a visit. The system is easy to use and very reliable.",
    },
    {
      name: "Marie Claire Uwamahoro",
      location: "Musanze",
      image: "/placeholder.svg?height=100&width=100&text=MCU",
      rating: 4,
      text: "I appreciate how easy it is to reschedule when needed. Life happens, and ONE HEALTHLINE CONNECT understands that. The booking system is flexible and user-friendly.",
    },
  ]

  const renderStars = (rating: number) => {
    return Array(5)
      .fill(0)
      .map((_, i) => (
        <Star
          key={i}
          className={`h-5 w-5 ${
            i < rating ? "text-yellow-400 fill-yellow-400" : "text-gray-300 dark:text-gray-600"
          }`}
        />
      ))
  }

  return (
    <section className="py-16 bg-white dark:bg-gray-900 transition-colors duration-300">
      <div className="container mx-auto px-4">
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold text-gray-900 dark:text-white mb-4 transition-colors duration-300">
            What Our <span className="text-green-600 dark:text-green-400">Patients Say</span>
          </h2>
          <p className="text-xl text-gray-600 dark:text-gray-300 max-w-3xl mx-auto transition-colors duration-300">
            Hear from people who have used our appointment booking service.
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {testimonials.map((testimonial, index) => (
            <div
              key={index}
              className="bg-gray-50 dark:bg-gray-800 rounded-xl p-6 shadow-sm hover:shadow-md transition-shadow duration-300"
            >
              <div className="flex items-center mb-4">
                <div className="relative w-16 h-16 rounded-full overflow-hidden mr-4">
                  <Image
                    src={testimonial.image || "/placeholder.svg"}
                    alt={testimonial.name}
                    fill
                    className="object-cover"
                  />
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-gray-900 dark:text-white transition-colors duration-300">
                    {testimonial.name}
                  </h3>
                  <p className="text-gray-600 dark:text-gray-300 transition-colors duration-300">
                    {testimonial.location}
                  </p>
                  <div className="flex mt-1">{renderStars(testimonial.rating)}</div>
                </div>
              </div>
              <p className="text-gray-700 dark:text-gray-300 italic transition-colors duration-300">
                &quot;{testimonial.text}&quot;
              </p>
            </div>
          ))}
        </div>

        <div className="mt-12 bg-green-50 dark:bg-green-900 rounded-xl p-8 transition-colors duration-300">
          <div className="flex flex-col md:flex-row items-center">
            <div className="mb-6 md:mb-0 md:mr-8 md:w-1/3">
              <div className="bg-white dark:bg-gray-700 rounded-full w-20 h-20 flex items-center justify-center mx-auto md:mx-0 mb-4 transition-colors duration-300">
                <Star className="h-10 w-10 text-green-600 fill-green-600 dark:text-green-400 dark:fill-green-400" />
              </div>
              <div className="text-center md:text-left">
                <p className="text-3xl font-bold text-gray-900 dark:text-white transition-colors duration-300">
                  4.9/5
                </p>
                <p className="text-gray-600 dark:text-gray-300 transition-colors duration-300">
                  Average Rating
                </p>
              </div>
            </div>
            <div className="md:w-2/3 space-y-3">
              {[
                { stars: "5 Stars", percent: 85 },
                { stars: "4 Stars", percent: 12 },
                { stars: "3 Stars", percent: 3 },
                { stars: "2 Stars", percent: 0 },
                { stars: "1 Star", percent: 0 },
              ].map(({ stars, percent }, idx) => (
                <div key={idx} className="flex items-center">
                  <p className="w-24 text-sm text-gray-600 dark:text-gray-300">{stars}</p>
                  <div className="flex-1 h-4 bg-gray-200 dark:bg-gray-700 rounded-full overflow-hidden mx-2">
                    <div
                      className="bg-green-600 dark:bg-green-400 h-full rounded-full transition-width duration-300"
                      style={{ width: `${percent}%` }}
                    ></div>
                  </div>
                  <p className="w-12 text-right text-sm text-gray-600 dark:text-gray-300">{percent}%</p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
